import { Link as GatsbyLink } from "gatsby";
import React from "react";
export const Link = ({ children, className, to, }) => {
    const internal = /^\/(?!\/)/.test(to);
    return internal ? (<GatsbyLink to={to} className={className}>
      {children}
    </GatsbyLink>) : (<a href={to} target="_blank" className={className}>
      {children}
    </a>);
};
